import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';

import { environment } from '@env/environment';

import { Package } from 'app/models/package';
import { Level } from 'app/models/level';

@Injectable({
  providedIn: 'root'
})
export class PackageService {

  apiURLPackages = environment.apiUrl + 'packages';
  apiURLLevels = environment.apiUrl + 'levels';


  public onChapterEditChanged: BehaviorSubject<any>;
  public apiData: any;
  constructor(private http: HttpClient) { 
     // Set the defaults
     this.onChapterEditChanged = new BehaviorSubject({});
  }

  getPackages(): Observable<Package[]> {

    return this.http.get<Package[]>(`${this.apiURLPackages}`);
  }

 
  /**
   * Get API Data
   */
   getPackage(packageId: string): Observable<Package> {
    return this.http.get<Package>(`${this.apiURLPackages}/${packageId}`);
  }


  createPackage(packageData: Package): Observable<Package> {
    return this.http.post<Package>(this.apiURLPackages, packageData);
  }

  updatePackage(packageData: Package): Observable<Package> {
    return this.http.put<Package>(`${this.apiURLPackages}/${packageData.id}`, packageData);
  }

  deletePackage(packageId: string): Observable<any> {
  
    return this.http.delete<any>(`${this.apiURLPackages}/${packageId}`);
  }


  /**
   * Get API Data --------------------Level------------------
   */

   getLevels(): Observable<Level[]> {

    return this.http.get<Level[]>(`${this.apiURLLevels}`);
  }

  /**
   * Get API Data
   */
   getLevel(levelId: string): Observable<Level> {
    return this.http.get<Level>(`${this.apiURLLevels}/${levelId}`);
  }


  createLevel(levelData: Level): Observable<Package> {
    return this.http.post<Level>(this.apiURLLevels, levelData);
  }

  updateLevel(levelData: Level): Observable<Level> {
    return this.http.put<Level>(`${this.apiURLLevels}/${levelData.id}`, levelData);
  }

  deleteLevel(levelId: string): Observable<any> {
  
    return this.http.delete<any>(`${this.apiURLLevels}/${levelId}`);
  }



}
